import React from "react";
import { Carousel } from "antd";
import { useStoreState } from "easy-peasy";
import { Skeleton } from "@material-ui/lab";
import { Link } from "react-router-dom";
const MyCarousel = (props) => {
  const { Featured, loading } = useStoreState((state) => state.vox);

  return (
    <div>
      {loading ? (
        <Skeleton variant="rect" animation="wave" width={1265} height={465} />
      ) : (
        <div>
          <Carousel fade dotPosition="left" autoplay>
            {Featured.slice(0, 5).map((post) => (
              <Link className="text-dark" key={post.id} to={`/post/${post.id}`}>
                <div className="car" key={post.id}>
                  <img
                    className="car-image"
                    src={post.imageUrl}
                    alt={post.title.toString()}
                  />
                  <p
                    className="car-text heading"
                    style={{ fontFamily: "Museo_Slab_500_2" }}
                  >
                    {post.title}
                  </p>
                  {/* <p className="car-text sub-heading" style={{  }}>
                      {post.description}
                        </p> */}
                </div>
              </Link>
            ))}
          </Carousel>
        </div>
      )}
    </div>
  );
};
export default MyCarousel;
