import React, { useEffect, createElement, useState } from "react";
import { useStoreState, useStoreActions } from "easy-peasy";
import ReactMarkdown from "react-markdown";
import { Comment, Tooltip, Avatar, Menu, Dropdown, BackTop } from "antd";
import one from "../../assets/boy-black-yellow.png";
import two from "../../assets/boy-brown-curly.png";
import three from "../../assets/boy-white-blue.png";
import four from "../../assets/girl-black-green.png";
import five from "../../assets/girl-white-green.png";
import six from "../../assets/girl-white-red-long-hair.png";
import Moment from "react-moment";
import Fire from "../../assets/fire.svg";
import Love from "../../assets/love.svg";
import Wow from "../../assets/wow.svg";
import Laugh from "../../assets/Laugh.svg";
import Sad from "../../assets/sad.svg";
import Anger from "../../assets/Anger.svg";
import { Skeleton } from "@material-ui/lab";
import { Link } from "@material-ui/core";
import { Helmet } from "react-helmet";
import { Navbar, Nav, Form, FormControl, Button } from "react-bootstrap";
import {
  DislikeOutlined,
  LikeOutlined,
  DislikeFilled,
  LikeFilled,
} from "@ant-design/icons";

const Posts = (props) => {
  const { post, loading, totalReactions } = useStoreState((state) => state.vox);
  const {
    getPosts,
    getPost,
    perfromCommentAdd,
    // performReplyAdd,
    performSuperbReaction,
    performSadReaction,
    performWowReaction,
    performLikeReaction,
    performLaughReaction,
    performAngeryReaction,
  } = useStoreActions((state) => state.vox);

  const [commentor, setCommentor] = useState("");
  const [comment, setComment] = useState("");
  const [skey, setskey] = useState(one);
  const id = post.id;
  const commentObject = {
    comment,
    commentor,
    skey,
  };

  useEffect(() => {
    // eslint-disable-next-line

    getPosts();
    // eslint-disable-next-line

    getPost(props.match.params.id);
    // eslint-disable-next-line
  }, []);
  const size = window.screen.width;
  const [likes, setLikes] = useState(0);
  const [dislikes, setDislikes] = useState(0);
  const [action, setAction] = useState(null);
  const like = () => {
    setLikes(1);
    setDislikes(0);
    setAction("liked");
  };

  const dislike = () => {
    setLikes(0);
    setDislikes(1);
    setAction("disliked");
  };
  const actions = [
    <Tooltip key="comment-basic-like" title="Like">
      <span onClick={like}>
        {createElement(action === "liked" ? LikeFilled : LikeOutlined)}
        <span className="comment-action">{likes}</span>
      </span>
    </Tooltip>,
    <Tooltip key="comment-basic-dislike" title="Dislike">
      <span onClick={dislike}>
        {React.createElement(
          action === "disliked" ? DislikeFilled : DislikeOutlined
        )}
        <span className="comment-action">{dislikes}</span>
      </span>
    </Tooltip>,
    <span key="comment-basic-reply-to">Reply to</span>,
  ];

  // const handleReplySubmit = (e) => {
  //   e.preventDefault();
  //   performReplyAdd({ cid, replyObject });
  //   setReply("");
  //   setRepliedBy("");
  // };
  //   const [statereply, setReply] = useState("");
  //   const [staterepliedBy, setRepliedBy] = useState("");
  //   const [cid, setCid] = useState("");
  //   const replyObject = {
  //     statereply,
  //     staterepliedBy,
  //   };

  const handleSubmitComment = (e) => {
    e.preventDefault();
    setCommentor("");
    setComment("");
    perfromCommentAdd({ id, commentObject });
  };
  const menu = (
    <Menu className="avatar-picker">
      <Menu.Item key="one" onClick={() => setskey(one)}>
        <img src={one} width="50px" alt="Avatar" />
      </Menu.Item>
      <Menu.Item key="two" onClick={() => setskey(two)}>
        <img src={two} width="50px" alt="Avatar" />
      </Menu.Item>
      <Menu.Item key="three" onClick={() => setskey(three)}>
        <img src={three} width="50px" alt="Avatar" />
      </Menu.Item>
      <Menu.Item key="four" onClick={() => setskey(four)}>
        <img src={four} width="50px" alt="Avatar" />
      </Menu.Item>
      <Menu.Item key="five" onClick={() => setskey(five)}>
        <img src={five} width="50px" alt="Avatar" />
      </Menu.Item>
      <Menu.Item key="six" onClick={() => setskey(six)}>
        <img src={six} width="50px" alt="Avatar" />
      </Menu.Item>
    </Menu>
  );
  const date = post.postedOn;
  const cDate = comment.commentedOn;
  return (
    <div className="container">
      <Helmet>
        <meta charset="uft-8" />
        <title>{post.title}</title>
        <meta name="description" content={post.description} />
      </Helmet>
      <Navbar
        fixed="top"
        style={{ backgroundColor: "#012e4f", height: "40px" }}
        variant="dark"
      >
        <Navbar.Brand>
          <Link
            className="text-light"
            style={{
              fontFamily: "Objective-Bold",
              fontSize: "28px",
            }}
            to="/"
          >
            Telugu
          </Link>
          <Link
            className="text-light"
            style={{
              fontFamily: "Objective-Light",
              fontSize: "28px",
            }}
            to="/"
          >
            phantom
          </Link>
        </Navbar.Brand>
        <Nav className="mr-auto"></Nav>
        
        <svg id="followFBSvg" width="44" height="44" viewBox="0 0 44 44">
          <path
            class="pathSocialIcons"
            d="M23.23 30.5v-7.754h2.603l.39-3.023H23.23v-1.929c0-.875.243-1.471 1.498-1.471l1.6-.001v-2.703a21.367 21.367 0 0 0-2.332-.119c-2.307 0-3.887 1.408-3.887 3.995v2.228H17.5v3.023h2.609V30.5h3.121z"
            fill="#FFF"
          ></path>
        </svg>
        <svg id="followTWSvg" width="44" height="44" viewBox="0 0 44 44">
          <path
            class="pathSocialIcons"
            d="M29.582 18.724c0-.164 0-.328-.01-.49a7.709 7.709 0 0 0 1.888-1.96 7.538 7.538 0 0 1-2.175.596 3.802 3.802 0 0 0 1.665-2.095 7.596 7.596 0 0 1-2.404.92 3.792 3.792 0 0 0-6.456 3.453 10.75 10.75 0 0 1-7.805-3.957 3.79 3.79 0 0 0 1.173 5.056 3.749 3.749 0 0 1-1.72-.474v.048a3.79 3.79 0 0 0 3.04 3.713 3.773 3.773 0 0 1-1.71.065 3.793 3.793 0 0 0 3.538 2.63A7.61 7.61 0 0 1 13 27.8a10.713 10.713 0 0 0 5.806 1.698c6.966.003 10.776-5.768 10.776-10.773z"
            fill="#FFF"
          ></path>
        </svg>
      </Navbar>
      <div className="post-body">
        <h4 className="h">{post.title}</h4>
        <h6 className="date">
          Published <Moment fromNow>{date}</Moment>
        </h6>
        <div>
          <h6>Tags</h6>
          {post.tags?.map((tag) => (
            <Link
              to={`/post/search/${tag.name}`}
              style={{ color: "Black", textDecoration: "none" }}
            >
              <button
                className=" btn btn-small black"
                style={{ marginRight: "5px", fontSize: "10px" }}
                key={tag.id}
              >
                {tag.name}
              </button>
            </Link>
          ))}
        </div>
        <div className="comments-likes">
          <h6 className="">
            Comments <span>{post.totalComments} </span>
          </h6>
          <h6 className="">
            Reactions <span>{totalReactions} </span>
          </h6>
          <h6 className="">
            Views <span>{post.views} </span>
          </h6>
        </div>
        {loading ? (
          size <= 400 ? (
            <Skeleton variant="rect" className="postImage" height={300} />
          ) : (
            <Skeleton variant="rect" className="postImage" height={600} />
          )
        ) : (
          <img className="postImage" src={post.imageUrl} alt={post.title} />
        )}

        <ReactMarkdown className="content" escapeHtml={false}>
          {post.content}
        </ReactMarkdown>
      </div>
      <h6 className="font-weight-bold">
        What's your Reaction about this article?
      </h6>
      <div className="reactions">
        <div className="r-item">
          <ul>
            <li>
              <center>{post.superb}</center>
            </li>
            <li>
              <img
                alt="Fire"
                className="link buzz-out-on-hover"
                src={Fire}
                height="50px"
                name="superb"
                onClick={() => performSuperbReaction(post.id)}
              />
            </li>
            <center>
              <li>Superb</li>
            </center>
          </ul>
        </div>
        <div className="r-item">
          <ul>
            <li>
              <center>{post.likes}</center>
            </li>
            <li>
              <img
                alt="Love"
                className="link buzz-out-on-hover"
                src={Love}
                height="50px"
                name="like"
                onClick={() => performLikeReaction(post.id)}
              />
            </li>
            <center>
              <li>Love</li>
            </center>
          </ul>
        </div>
        <div className="r-item">
          <ul>
            <li>
              <center>{post.wow}</center>
            </li>
            <li>
              <img
                alt="Wow"
                className="link buzz-out-on-hover"
                src={Wow}
                height="50px"
                name="wow"
                onClick={() => performWowReaction(post.id)}
              />
            </li>
            <center>
              <li>Wow</li>
            </center>
          </ul>
        </div>
        <div className="r-item">
          <ul>
            <li>
              <center>{post.sad}</center>
            </li>
            <li>
              <img
                alt="Sad"
                className="link buzz-out-on-hover"
                src={Sad}
                height="50px"
                name="sad"
                onClick={() => performSadReaction(post.id)}
              />
            </li>
            <center>
              <li>Sad</li>
            </center>
          </ul>
        </div>
        <div className="r-item">
          <ul>
            <li>
              <center>{post.laugh}</center>
            </li>
            <li>
              <img
                alt="Laugh"
                className="link buzz-out-on-hover"
                src={Laugh}
                height="50px"
                name="laugh"
                onClick={() => performLaughReaction(post.id)}
              />
            </li>
            <center>
              <li>Laugh</li>
            </center>
          </ul>
        </div>
        <div className="r-item">
          <ul>
            <li>
              <center>{post.angery}</center>
            </li>
            <li>
              <img
                alt="Anger"
                className="link buzz-out-on-hover"
                src={Anger}
                height="50px"
                name="anger"
                onClick={() => performAngeryReaction(post.id)}
              />
            </li>
            <center>
              <li>Angery</li>
            </center>
          </ul>
        </div>
      </div>
      <div>
        <h6 className="font-weight-bold mt-0">Write Comment</h6>
        <div className="">
          <img
            src={skey}
            width="60px"
            className="p-2"
            alt="selected avatar"
            style={{ borderRadius: "50px" }}
          />
          <Dropdown overlay={menu}>
            <span
              className="ant-dropdown-link text-dark font-weight-bold"
              onClick={(e) => console.log("e")}
            >
              Choose Avatar
            </span>
          </Dropdown>
          <form>
            <div>
              <input
                type="text"
                value={commentor}
                style={{
                  width: "200px",
                  padding: "10px",
                  marginBottom: "10px",
                  borderRadius: "8px",
                }}
                name="commentor"
                onChange={(e) => setCommentor(e.target.value)}
                placeholder="Enter you name"
              />
            </div>
            <div>
              <input
                rows={4}
                name="comment"
                style={{
                  paddingBottom: "100px",
                  paddingTop: "10px",
                  paddingLeft: "10px",
                  width: "90%",
                  marginBottom: "10px",
                  borderRadius: "8px",
                }}
                onChange={(e) => setComment(e.target.value)}
                placeholder="Write your comment here"
                value={comment}
              />
            </div>
            <button
              className="btn btn-sm btn-dark"
              type="submit"
              onClick={handleSubmitComment}
            >
              Comment
            </button>
          </form>
        </div>
        {post.comments?.map((comment) => (
          <div key={comment.id}>
            <Comment
              actions={actions}
              avatar={<Avatar src={comment.skey} alt="{commentor}" />}
              key={comment.id}
              author={comment.commentor}
              content={comment.comment}
              datetime={<Moment fromNow>{cDate}</Moment>}
            />
            <div
              style={{
                backgroundColor: "floralwhite",
                display: "flex",
                justifyContent: "flex-end",
                flexDirection: "row",
              }}
            ></div>
          </div>
        ))}
      </div>
      <BackTop />
    </div>
  );
};
export default Posts;
