import React, { useState, useEffect } from "react";
import axios from "axios";
import { domain } from "../../../src/urls.js";
import { Skeleton } from "antd";
import { Link } from "react-router-dom";
import { Navbar, Nav, } from "react-bootstrap";
const SearchResult = (props) => {
  const { keyword } = props.match.params;
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const getPosts = () => {
    axios
      .get(`${domain}/post/search/${keyword}`)
      .then((res) => {
        setPosts(res.data.data);
        setLoading(false);
      })
      .then(console.log(posts))
      .catch((err) => {
        console.error(err);
      });
  };
  useEffect(() => {
    getPosts();
  }, []);

  const string = () => {
    if (loading) {
      return "Searching";
    } else {
      return "Search results for";
    }
  };
  const renderResults = string();
  return (
    <div className="search-body">
      <Navbar fixed="top" bg="dark" variant="dark">
        <Navbar.Brand>
          <Link className="text-light" to="/">
            TeluguPhantom
          </Link>
        </Navbar.Brand>
        <Nav className="mr-auto"></Nav>
      </Navbar>
      <div className="ml-4">
        {renderResults}
        <h3>'{keyword}'</h3>
        <div className="back">
          <button className="btn btn-dark mb-5" style={{ marginLeft: "90%" }}>
            <Link className="text-light" to="/">Back</Link>
          </button>
        </div>
      </div>
      <div className="container">
        <div className="row">
          <div className="col-md-10">
            <div className="result">
              {posts.map((post) => (
                <Link className="text-dark" to={`/post/${post.id}`}>
                  <div className="searchresult">
                    <h5>{post.title}</h5>
                    <p>{post.description}</p>
                  </div>
                </Link>
              ))}
            </div>
          </div>
          <div className="col"></div>
        </div>
      </div>
    </div>
  );
};

export default SearchResult;
